.cmp-Dialog__dialog__root {
    /* The root aside element takes up the whole screen. */
    position: fixed;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;

    /* It is hidden by default. */
    opacity: 0;
    visibility: hidden;

    /* It animates to being closed. */
    transition-duration: 192ms;
    transition-timing-function: var(--venia-global-anim-out);
    transition-property: opacity, visibility;

    /* It sits over all background content. */
    z-index: 3;
}

.cmp-Dialog__dialog__root_open {

    opacity: 1;
    visibility: visible;

    /* It animates to being open. */
    transition-duration: 224ms;
    transition-timing-function: var(--venia-global-anim-in);
}

.cmp-Dialog__dialog__form {
    /* The form fills the entire aside. */
    height: 100%;
    width: 100%;

    /* Its contents are centered horizontally and vertically. */
    display: grid;
    justify-content: center;
    align-content: center;
}

.cmp-Dialog__dialog__mask {
    /* The mask takes up the entire screen. */
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;

    /* The mask is a semi-transparent grey. */
    background-color: rgb(var(--venia-global-color-gray-darker));
    opacity: 0.5;
}

/* The dialog is the only item in the form grid. */
.cmp-Dialog__dialog__dialog {
    background-color: rgb(var(--venia-global-color-background));
    border-radius: 5px;
    box-shadow: 1px 1px 5px rgb(var(--venia-global-color-gray-darker));

    /* It sets maximum sizes so its body can handle overflow. */
    height: 100%;
    max-height: 90vh;
    /* Minimum keeps a 16:9 aspect ratio and is 40rem x 22.5rem.  */
    min-height: 360px;
    width: 640px;

    /* Nothing is allowed to overflow container itself. */
    overflow: hidden;

    /* Container is itself a grid container for its children. */
    display: grid;
    grid-template-rows: auto 1fr;

    /* Container can be the target of pointer events. */
    pointer-events: auto;

    /* It sits on top of the mask. */
    z-index: 4;
}

/*
 *  Dialog Header styles.
 */

.cmp-Dialog__dialog__header {
    border-bottom: 1px solid rgb(var(--venia-global-color-border));
    height: 3.5rem;
    padding: 0 0.75rem 0 1rem;

    /* The Header is itself a grid container for its children. */
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: 1fr auto;
    column-gap: 1rem;
    align-items: center;
}

.cmp-Dialog__dialog__headerText {
    color: rgb(var(--venia-global-color-text-alt));
    text-transform: capitalize;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    line-height: 1.25rem;
}

.cmp-Dialog__dialog__headerButton {
    /* Horizontally align the close button to the right. */
    justify-self: right;
}

/*
 *  Dialog Body styles.
 */

.cmp-Dialog__dialog__body {
    overflow: auto;

    /* The Body is itself a grid container for its children. */
    display: grid;
    grid-template-rows: 1fr auto;
}

.cmp-Dialog__dialog__contents {
    padding: 1rem;
}

.cmp-Dialog__dialog__buttons {
    padding: 2.5rem;

    display: grid;
    grid-auto-flow: column;
    gap: 1rem;
    justify-content: center;
}

.cmp-Dialog__dialog__confirmButton {
}

/*
 * Mobile-specific styles.
 *
 * Instead of being a full-page modal,
 * dialogs slide out from the right.
 */

@media (max-width: 960px) {
    .cmp-Dialog__dialog__root {
        left: auto;
        right: 0;

        /* The Dialog starts off-screen on the right. */
        transform: translate3d(100%, 0, 0);
        /* For mobile, add position to the transitions. */
        transition-property: opacity, transform, visibility;
    }

    .cmp-Dialog__dialog__root_open {
        /* The Dialog animates (slides) onto the screen. */
        transform: translate3d(0, 0, 0);
    }

    .cmp-Dialog__dialog__form {
        align-content: stretch;
        justify-content: end;
    }

    .cmp-Dialog__dialog__dialog {
        max-height: 100%;
        max-width: 360px;
    }

    .cmp-Dialog__dialog__buttons {
        grid-auto-flow: row;
    }

    .cmp-Dialog__dialog__confirmButton {
        /* On mobile the confirm button should be first (on top). */
        order: -1;
    }
}
